  content: 'âœ“';
  font-size: 20px;
  display: none;
}

.done .tick::before {
  display: inline;
} */

.delete-todo {
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

.delete-todo svg {
  width: 30px;
  height: 30px;
  pointer-events: none;
}

form {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
}

input[type="text"] {
  width: 100%;
  padding: 10px;
  border-radius: 4px;
  border: 3px solid #333;
}
.checked{background-color: rgba(66, 24, 44, 0.822);}
.unchecked{background-color: transparent;}
.checkedspan{text-decoration: line-through}
.none{
  text-decoration:none;
}